/* –ü–æ–ª–Ω–æ–µ –ø–æ–∫—Ä—ã—Ç–∏–µ —ç–∫—Ä–∞–Ω–∞ */
html, body {
    height: 100vh;
    width: 100vw;
    margin: 0;
    padding: 0;
    font-family: "Arial", sans-serif;
    background: #000;
    color: #fff;
    display: flex;
    flex-direction: column;
    text-align: center;
    overflow: hidden;
    overscroll-behavior: none;
    touch-action: none;
}

/* –û—Ç–∫–ª—é—á–µ–Ω–∏–µ –≤—ã–¥–µ–ª–µ–Ω–∏—è —Ç–µ–∫—Å—Ç–∞ */
* {
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}

/* –ì–ª–∞–≤–Ω—ã–π –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä */
.app {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 100vh;
    width: 100%;
    position: relative;
    z-index: 1;
}

/* –ö–æ–Ω—Ç–µ–Ω—Ç */
.scrollable-content {
    flex: 1;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    padding: 20px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 2;
}

/* –ü–∞–Ω–µ–ª—å –Ω–∞–≤–∏–≥–∞—Ü–∏–∏ */
.tab-bar {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    justify-items: center;
    width: 216px;
    height: 52px;
    background: #141414;
    border-radius: 26px;
    position: fixed;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
    z-index: 10;
}

/* –ü–ª–∞–≤–Ω—ã–π –∏–Ω–¥–∏–∫–∞—Ç–æ—Ä */
.active-indicator {
    position: absolute;
    width: 72px;
    height: 52px;
    background: rgba(255, 255, 255, 0.15);
    border-radius: 26px;
    top: 0;
    left: 0;
    transition: left 0.3s ease-in-out;
    z-index: 1;
}

/* –ö–Ω–æ–ø–∫–∏ */
.tab-bar button {
    width: 72px;
    height: 52px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
    border: none;
    cursor: pointer;
    z-index: 2;
}

/* –ò–∫–æ–Ω–∫–∏ */
.button-icon {
    width: 28px;
    height: 28px;
    filter: brightness(0.7);
    transition: filter 0.3s ease-in-out, transform 0.2s ease-in-out;
}

/* –ò–∑–º–µ–Ω–µ–Ω–∏–µ —Ü–≤–µ—Ç–∞ –∞–∫—Ç–∏–≤–Ω–æ–π –∫–Ω–æ–ø–∫–∏ */
.tab-bar button.active .button-icon {
    filter: brightness(2) contrast(1.2);
    transform: scale(1.1);
}


.background-canvas {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 0;
}
